import * as React from 'react'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import { Link } from 'gatsby'
import {card, cardButton} from './layout.module.css'

function Card(props) {
    return (
        <div className={card}>
            <GatsbyImage
                image={props.image}
                alt={props.alt}
            />
            <h2>{props.tittle}</h2>
            <p>{props.description}</p>
            <p>
            <Link to={`../../projects/${props.slug}`}>
                <button className={cardButton}>View more...</button>
            </Link>
            </p>
        </div>
    );
}

export default Card;